/* 페이지 타이틀 */
.text_line {
    width: 100%;
    height: 100%;
    position: absolute;
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: dash 3s linear forwards, filling 2s ease-in forwards, moveUp 1s 3s forwards;
    font-size: 100px;
}
.text_line text { animation:textSize 1s 3s forwards; }
/* 텍스트 fade in 애니메이션 */
@keyframes dash {
    to { stroke-dashoffset: 0; }
}
@keyframes filling {
    0%, 90% { fill: black; fill-opacity: 0; }
    100% { fill: black; fill-opacity: 1; }
}
/* 텍스트 상단 이동 및 글자 크기 조정 */
@keyframes moveUp{
    0%{ top:0%; }
    100%{ top:-35%; }
}
@keyframes textSize{
    0%{ font-size:100px; }
    100%{ font-size:70px; }
}


/* 페이지 */
.wrapper{
    display: -ms-grid;
    display: grid;
    -ms-grid-rows: 35px 8px 1fr;
    grid-template-rows: 35px 8px 1fr;
    position: absolute;
    width: 900px;
    height: 58%;
    left: 50%;
    margin-left: -480px;
    padding: 30px;
    border-radius: 20px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    -webkit-box-shadow: 2px 7px 15px 8px rgba(0,0,0,0.3);
            box-shadow: 2px 7px 15px 8px rgba(0,0,0,0.3);
    -webkit-animation: fadeInWrapper 0.8s forwards;
            animation: fadeInWrapper 0.8s forwards;
}
@-webkit-keyframes fadeInWrapper{
    0%{ top:110%; }
    100%{ top:25%; }
}
@keyframes fadeInWrapper{
    0%{ top:110%; }
    100%{ top:25%; }
}

/* 메뉴 */
.menu{
    line-height: 35px;
    font-size: 22px;
    font-weight: bold;
    transform: translateY(-50px);
    -webkit-transform: translateY(-50px);
    opacity: 0;
    -webkit-animation: fadeInMenu 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: fadeInMenu 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
@-webkit-keyframes fadeInMenu {
    0% {
        -webkit-transform: translateY(-50px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateY(0);
        opacity: 1;
    }
}
@keyframes fadeInMenu {
    0% {
        transform: translateY(-50px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

/* 스크롤바 */
.scroll{
    height: 3px;
    background: rgb(230, 230, 230);
    opacity: 0;
    animation: fadeInScrollbar 0.5s 0.6s forwards;
}
.scroll > span{
    content: '';
    height: 3px;
    position: absolute;
    background: black;
}
@keyframes fadeInScrollbar{ 100%{ opacity:1; } }


/* 프로젝트 콘텐츠 */
.content{
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    overflow: auto;
}
.content::-webkit-scrollbar{ display: none; }
.content .per{
    width: 173px;
    height: 173px;
    margin: 8px;
    border: 1px solid white;
    border-radius: 10px;
    background: skyblue;
    -webkit-animation: fadeInContent 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	animation: fadeInContent 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
.content .per:hover{
    border: 1px solid black;
}
@-webkit-keyframes fadeInContent {
    0% {
        -webkit-transform: scaleY(0);
        opacity: 1;
    }
    100% {
        -webkit-transform: scaleY(1);
        opacity: 1;
    }
}
@keyframes fadeInContent {
    0% {
        transform: scaleY(0);
        opacity: 1;
    }
    100% {
        transform: scaleY(1);
        opacity: 1;
    }
}
